@font-face {
  font-family: 'Roboto Slab';
  src: url('#{$font-path}/roboto/RobotoSlab-Bold.eot');
  src: url('#{$font-path}/roboto/RobotoSlab-Bold.eot?#iefix') format('embedded-opentype'),
  url('#{$font-path}/roboto/RobotoSlab-Bold.woff2') format('woff2'),
  url('#{$font-path}/roboto/RobotoSlab-Bold.woff') format('woff'),
  url('#{$font-path}/roboto/RobotoSlab-Bold.ttf') format('truetype'),
  url('#{$font-path}/roboto/RobotoSlab-Bold.svg#RobotoSlab-Bold') format('svg') {}
  font-weight: bold;
  font-style: normal; }


