%clear {
  &:after {}
  content: "";
  display: table;
  clear: both; }

%transition {
  transition: all .2s ease-out; }

@keyframes bounce {
  0% {
    transform: translateY(0%) scaleX(1.4); }
  50% {
    transform: translateY(30%) scaleX(1.1); }
  100% {
    transform: translateY(0%) scaleX(1.4); } }
