::selection {
  color: $white;
  background-color: $accent-color; }

* {
  box-sizing: border-box; }

html {
  font-family: sans-serif;
  line-height: 1.15;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%; }

body {
  margin: 0;
  font-family: $base-font;
  font-size: 16px;
  line-height: 1.4;
  text-transform: lowercase;
  color: $gray;
  scroll-behavior: smooth; }

figcaption,
article,
section {}
footer,
header,
figure,
aside,
main,
nav {
  display: block; }

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible; }

pre {
  font-family: monospace, monospace;
  font-size: 1em; }

img {
  max-width: 100%;
  border-style: none; }

code,
kbd,
samp {
  text-transform: none;
  font-family: monospace, monospace;
  font-size: 1em; }

small {
  font-size: 80%; }

audio,
video {
  display: inline-block; }

svg:not(:root) {
  overflow: hidden; }

.wrapper {
  background-color: $white;
  position: relative;
  z-index: 1; }

.container {
  margin: 0 auto;
  padding-top: 60px;
  padding-left: 15px;
  padding-right: 15px;
  padding-bottom: 100px;
  max-width: 700px;
  & > h1 {
    font-size: em(40);
    text-align: center;
    color: rgba($bg-color, .3);
    margin-bottom: .2em; }
  &-page,
  &-article {
    font-size: 18px;
    a {
      border-bottom: 1px dashed $bg-color;
      &:hover {
        border-bottom-color: transparent; } } } }

a {
  color: $bg-color;
  text-decoration: none;
  -webkit-text-decoration-skip: objects;
  @extend %transition;
  &:hover,
  &:active {
    outline-width: 0;
    color: $white;
    background: $accent-color; } }

.adsense {
  margin-top: 60px;
  .container-article & {
    margin-top: 0; } }

.social-button.social-button {
  width: 50px;
  display: inline-block;
  border-bottom: 0;
  text-align: center; }
